}
.icon-tag.tag-blueBd {
    border: 1px solid #4a90e2;
    border-radius: 2px;
    color: #4a90e2;
}
@media screen and (-webkit-min-device-pixel-ratio: 2) {
.icon-tag.tag-blueBd {
        border-radius: 0;
        position: relative;
        border: 0;
}
.icon-tag.tag-blueBd:before {
          content: "";
          width: 200%;
          height: 200%;
          position: absolute;
          top: 0;
          left: 0;
          border: 1px solid #4a90e2;
          -webkit-transform: scale(0.5);
          -webkit-transform-origin: 0 0;
          padding: 1px;
          -webkit-box-sizing: border-box;
          border-radius: 2px;
          pointer-events: none;
}
}
