.set-popup {
  border: 1px solid #e13f2c;
  border-radius: 4px;
  display: inline-block;
  position: absolute !important;
  left: 0px;
  top: -58px;
  width: 150px;
  padding: 6px 10px;
  line-height: 18px;
  background-color: #ffffff;
  font-size: 11px;
  text-align: center;
  font-weight: normal;
}
@media screen and (-webkit-min-device-pixel-ratio: 2) {
.set-popup {
      border-radius: 0;
      position: relative;
      border: 0;
}
.set-popup:before {
        content: "";
        width: 200%;
        height: 200%;
        position: absolute;
        top: 0;
        left: 0;
        border: 1px solid #e13f2c;
        -webkit-transform: scale(0.5);
        -webkit-transform-origin: 0 0;
        padding: 1px;
        -webkit-box-sizing: border-box;
        border-radius: 4px;
        pointer-events: none;
}
}
.set-popup .icon {
    display: inline-block;
    width: 0;
    height: 0;
    border: solid transparent;
    border-width: 8px;
    border-top-color: #e13f2c;
    content: "";
    position: absolute;
    bottom: -23px;
    right: 60px;
}
.set-popup .icon:after {
      display: inline-block;
      width: 0;
      height: 0;
      border: solid transparent;
      border-width: 8px;
      border-top-color: #ffffff;
      content: "";
      position: absolute;
      bottom: -7px;
      right: -8px;
}
